<template>
  <div>
    <div class="topButton">
      <el-button size="mini" type="primary" @click="openAdd()"></el-button>

      <el-button size="mini" type="warning" @click="openGet()"></el-button>
      
      <el-button size="mini" type="info" @click="postM()"></el-button>
      <el-button size="mini" type="info" @click="handleDownload" :loading="downloadLoading"></el-button>
    </div>
    <!--  -->
    <div class="list">
      <el-table
        :data="material.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        style="width: 100%"
        highlight-current-row
        border
        @current-change="handleCurrentsChange"
      >
        <el-table-column label="" prop="materialNum"></el-table-column>

        <el-table-column label="" prop="materialName"></el-table-column>

        <!-- <el-table-column label="" prop="materialPostTime"></el-table-column>

        <el-table-column label="" prop="materialPassTime"></el-table-column> -->

        <el-table-column label="" prop="price"></el-table-column>

        <el-table-column label="" prop="materialState"></el-table-column>

        <el-table-column label="" prop="materialTypeName"></el-table-column>
        <el-table-column label="" prop="rejectRemark"></el-table-column>
        <el-table-column label="" prop="remark"></el-table-column>
      </el-table>

      <!--  -->
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 40]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="material.length"
        ></el-pagination>
      </div>
    </div>

    <!--  -->
    <div v-if="flag2" class="getM1">
      <div class="title">
        <h2></h2>
        <i class="el-icon-close" @click="clossAdd()"></i>
      </div>

      <div class="change">
        <el-form ref="form" :model="addM" label-width="80px">
          <el-form-item label="">
            <el-input v-model="addM.materialName"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="addM.price"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-select v-model="addM.materialTypeName" placeholder="" style="width:270px;">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="addM.poster"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="addM.remark"></el-input>
          </el-form-item>

          <div class="addButton">
            <el-form-item>
              <el-button type="primary" @click="addMa()"></el-button>
              <el-button @click="clossAdd()"></el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>
    <!--  -->
    <div v-if="flag" class="getM">
      <div class="title">
        <h2></h2>
        <i class="el-icon-close" @click="closeGet()"></i>
      </div>

      <div class="change">
        <el-form ref="form" :model="changematerial" label-width="80px">
          <el-form-item label="">
            <el-input v-model="changematerial.materialName"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="changematerial.price"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-select
              v-model="changematerial.materialTypeName"
              placeholder=""
              style="width:270px;"
            >
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="changematerial.poster"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="changematerial.remark"></el-input>
          </el-form-item>

          <div class="addButton">
            <el-form-item>
              <el-button type="primary" @click="change()"></el-button>
              <el-button @click="closeGet()"></el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>
    <!--  -->
    <div class="popContainer" v-if="flag"></div>
    <div class="popContainer" v-if="flag2"></div>
  </div>
</template>

<script>
import { close } from "fs";
import { constants } from "crypto";
export default {
  data() {
    return {
      material: [],
      currentPage: 1, //
      pagesize: 10,
      changematerial: [
        {
          id: "",
          materialName: "",
          price: "",
          materialTypeName: "",
          poster: "",
          remark: "",
          materialState: ""
        }
      ],
      addM: [
        {
          materialName: "",
          price: "",
          materialTypeName: "",
          poster: "",
          remark: ""
        }
      ],
      flag: false,
      flag2: false,
      flag3: false,
      downloadLoading: false
    };
  },
  inject: ["reload"],
  created() {
    let _this = this;
    this.$axios
      .get("/materials")
      .then(function(res) {
        if (res.data) {
          _this.material = res.data;
        }
      })
      .catch(function(err) {
        if (err.response) {
          console.log(err.response);
        }
      });
  },
  methods: {
    setCurrent(row) {
      this.$refs.singleTable.setCurrentRow(row);
    },
    handleCurrentsChange(val) {
      this.currentRow = val;

      this.changematerial = val;
    },

    handleSizeChange: function(size) {
      this.pagesize = size;
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
    },
    openGet() {
      if (this.changematerial.id) {
        this.flag = !this.flag;
      } else {
        this.$message("");
      }
    },
    closeGet() {
      this.flag = !this.flag;
      this.$message("");
    },
    openAdd() {
      this.flag2 = !this.flag2;
    },
    clossAdd() {
      this.flag2 = !this.flag2;
    },
    addMa() {
      // let _this = this;
      // this.$axios
      //   .post("/materials", {
      //     materialName: _this.addM.materialName,
      //     price: _this.addM.price,
      //     materialTypeName: _this.addM.materialTypeName,
      //     poster: _this.addM.poster,
      //     remark: _this.addM.remark
      //   })
      //   .then(res => {
      //     if (res.data.code === 200) {
      //       this.$message("");
      //       this.flag2 = !this.flag2;
      //       this.reload();
      //     }
      //   })
      //   .catch(function(err) {
      //     if (err.response) {
      //       console.log(err.response);
      //     }
      //   });
      if(this.addM.materialName){
        let _this = this;
      this.$axios
        .post("/materials", {
          materialName: _this.addM.materialName,
          price: _this.addM.price,
          materialTypeName: _this.addM.materialTypeName,
          poster: _this.addM.poster,
          remark: _this.addM.remark
        })
        .then(res => {
          if (res.data.code === 200) {
            this.$message("");
            this.flag2 = !this.flag2;
            this.reload();
          }
        })
        .catch(function(err) {
          if (err.response) {
            console.log(err.response);
          }
        });
      }else{
        this.$message('')
      }
    },
    postM() {
      if (this.changematerial.materialState) {
        let _this = this;
        this.$axios
          .put("/materialState", {
            id: _this.changematerial.id,
            materialState: _this.changematerial.materialState
          })
          .then(res => {
            if (res.data.code === 200) {
              this.$message("");
              this.flag = !this.flag;
              this.reload();
            }
          })
          .catch(function(err) {
            if (err.response) {
              console.log(err.response);
            }
          });
      } else {
        this.$message("");
      }
    },
    handleDownload() {
      this.downloadLoading = true;
      console.log(this.material);
      require.ensure([], () => {
        const { export_json_to_excel } = require("@/vendor/Export2Excel");
        const tHeader = [
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          ""
        ];
        const filterVal = [
          "materialNum",
          "materialName",
          "materialPostTime",
          "materialPassTime",
          "price",
          "materialState",
          "materialTypeName",
          "rejectRemark",
          "remark"
        ];
        const list = this.material;
        const data = this.formatJson(filterVal, list);
        export_json_to_excel(tHeader, data, "excel");
        this.downloadLoading = false;
      });
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    },
    change() {
      let _this = this;
      this.$axios
        .put("/materials", {
          id: _this.changematerial.id,
          materialName: _this.changematerial.materialName,
          price: _this.changematerial.price,
          materialTypeName: _this.changematerial.materialTypeName,
          poster: _this.changematerial.poster,
          remark: _this.changematerial.remark,
          materialState: _this.changematerial.materialState
        })
        .then(res => {
          if (res.data.code === 200) {
            this.$message("");
            this.flag = !this.flag;
            this.reload();
          }
        })
        .catch(function(err) {
          if (err.response) {
            console.log(err.response);
          }
        });
    }
  }
};
</script>

<style>
.topButton {
  width: 100%;
  height: 50px;
  line-height: 50px;
  padding-bottom: 20px;
}
.list {
  position: relative;
}
.page {
  /* margin-top: 20px;
    margin-left: 800px; */
  position: absolute;
  right: 0;
  bottom: -50px;
}
.getM {
  position: absolute;
  z-index: 1001;
  width: 400px;
  height: 490px;
  left: 0;
  top: -50px;
  bottom: 0;
  right: 0;
  margin: auto;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
}
.getM .title {
  height: 50px;
  width: 100%;
  text-align: center;
  line-height: 30px;
  /* border-bottom: 1px solid #dcdfe6; */
  color: #409eff;
  text-shadow: 2px 2px 2px #ccc;
}
.getM .title i {
  position: relative;
  top: -55px;
  right: -170px;
  color: black;
  font-size: 20px;
}
.getM .change {
  margin-right: 40px;
  margin-top: 30px;
  margin-left: 10px;
}
.getM .addButton .el-button {
  width: 115px;
}
.getM .el-select {
  width: 280px;
}

.getM1 {
  position: absolute;
  z-index: 1001;
  width: 400px;
  height: 490px;
  left: 0;
  top: -50px;
  bottom: 0;
  right: 0;
  margin: auto;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
}
.getM1 .title {
  height: 50px;
  width: 100%;
  text-align: center;
  line-height: 30px;
  /* border-bottom: 1px solid #dcdfe6; */
  color: #409eff;
  text-shadow: 2px 2px 2px #ccc;
}
.getM1 .title i {
  position: relative;
  top: -55px;
  right: -170px;
  color: black;
  font-size: 20px;
}
.getM1 .change {
  margin-right: 40px;
  margin-top: 30px;
  margin-left: 10px;
}
.getM1 .addButton .el-button {
  width: 115px;
}
.getM1 .el-select {
  width: 280px;
}
.popContainer {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 500;
}
</style>


